@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,400;0,600;1,400;1,600&display=swap");

body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
  background-color: rgb(245, 244, 244);
}

a {
  color: rgb(89, 97, 206);
  font-weight: 600;
  text-decoration: none;
}

h1 {
  font-family: "Open Sans", serif;
  font-size: 18px;
  font-weight: 600;
  line-height: 28px;
}

h2 {
  font-family: "Open Sans", serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 26px;
}

h3 {
  font-family: "Open Sans", serif;
  font-size: 13px;
  line-height: 23px;
  font-weight: 600;
}

p {
  font-family: "Open Sans", serif;
  font-size: 11px;
  font-weight: normal;
  line-height: 18px;
}

.app {
  position: relative;
}
.header {
  display: flex;
  justify-content: space-around;
  height: 40%;
  color: rgb(0, 0, 0);
  min-width: 1236px;
  background-color: rgba(47, 79, 79, 0.493);
}

.app-title a {
  color: rgb(0, 0, 0);
  width: 160px;
  padding: 8px;
  display: flex;
  position: relative;
  top: 15%;
  list-style-type: none;
  float: left;
  color: rgb(255, 255, 255);
  padding: 12px;
  margin: 5px;
  font-size: 20px;
  cursor: pointer;
  transition: font-size 0.1s ease;
}

.app-title a:hover {
  font-size: 22px;
}

.app-title p {
  margin-left: 20%;
  padding: 0%;
}

.app-title-logo img {
  width: 100px;
  position: relative;
  left: 72%;
}

.app-title-link {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.app-title-link a {
  list-style-type: none;
  float: left;
  color: rgb(255, 255, 255);
  padding: 12px;
  margin: 5px;
  font-size: 20px;
  cursor: pointer;
  transition: font-size 0.1s ease;
}

.app-title-link a:hover {
  font-size: 26px;
}

.app-title p {
  font-size: 19px;
  padding-right: 80px;
  margin-right: 120px;
  position: relative;
  bottom: 10px;
}

.main {
  position: relative;

  width: 100%;
}

.sidebar {
  position: absolute;
  top: 10px;
  left: 30px;
  list-style: none;
  padding: 0px;
  width: 150px;
  padding-right: 20px;
  border-right: 1px solid rgba(0, 0, 0, 0.1);
}

.sidebar li {
  color: rgb(255, 255, 255);
  font-size: 12px;
  font-weight: 600;
  border-radius: 20px;
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
  margin: 4px;
  background: linear-gradient(
    30deg,
    rgba(148, 0, 148, 1) 0%,
    rgba(255, 50, 82, 1) 100%
  );

  cursor: pointer;
  display: inline-block;
  transition: background-color 600ms, color 100ms;
}

.sidebar > a:last-child li {
  background: rgb(193, 0, 212);
  background: linear-gradient(
    30deg,
    rgba(193, 0, 212, 1) 0%,
    rgba(0, 205, 255, 1) 100%
  );
  color: white;
}

.content {
  width: 50%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  position: relative;
  left: 24%;
  top: 70px;
  padding: 20px;
}

.post {
  position: relative;
  background-color: #fff;
  padding: 20px;
  margin: 8px;
  width: 350px;
}

.post > a > h1 {
  position: relative;
  top: 10px;
  margin: 0px;
  color: black;
  margin-bottom: 5px;
}

.post > h3 {
  position: relative;
  top: 10px;
  margin: 0px;
  font-weight: normal;
  font-size: 13px;
  color: rgba(0, 0, 0, 0.7);
}

.post > p {
  position: relative;
  font-family: "Open Sans";
  top: 10px;
  font-size: 14px;
  white-space: pre-wrap;
}

.divider {
  width: 100%;
  height: 1px;
  background-color: rgba(0, 0, 0, 0.1);
}

.sign-in > h2,
.sign-up > h2 {
  margin: 0px;
  padding: 10px;
  font-size: 20px;
  font-weight: 600;
  color: rgb(0, 0, 0);
  display: inline-block;
  position: relative;
  left: 48%;
}

.signin-form > button {
  background-color: #007bff;
  color: #fff;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1em;
}

.signin-form,
.signup-form {
  position: relative;
  left: 35%;
  top: 50px;
  width: 30%;
  height: 400px;
  border-left: 1px solid;

  padding-left: 20px;
}

.signup-form > input,
.signup-form > button {
  background-color: #007bff;
  color: #fff;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1em;
}

.add-post-body {
  width: 50%;
  padding: 20px;
  position: relative;
  left: 38%;
}

.add-post > h2 {
  margin: 0px;
  padding: 10px;

  color: rgb(0, 0, 0);
  display: inline-block;
}

.add-post-form input {
  position: relative;
  width: 20%;
  height: 40%;
  border-left: 1px solid;
  top: 19px;
  left: 190px;
  padding-left: 20px;
}

#add-post-btn {
  position: relative;
  color: rgb(255, 255, 255);
  width: 80px;
  padding: 12px;
  margin: 5px;
  top: 4px;
  background-color: rgb(0, 0, 0);
  font-size: 13px;
  font-weight: 600;
  border-radius: 3px;
  outline: none;
  cursor: pointer;
  display: block;
}

.add-post-list {
  position: absolute;
  width: 450px;
  left: 20px;
  top: 70px;
  padding: 0px;
}

.add-post-list > li {
  position: relative;
  background-color: rgba(0, 0, 0, 0.9);
  padding: 5px;
  font-size: 13px;
  margin: 5px;
  color: white;
  display: inline-block;
}

.add-post h2 {
  font-size: 1.5em;
  color: #333;
  margin-bottom: 20px;
}

.add-post-form {
  display: flex;
  flex-direction: column;
  position: relative;
  top: 0px;
  width: 40%;
}

.label {
  font-weight: bold;
  margin-bottom: 5px;
}

.text-area {
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1em;
  resize: vertical;
}

.char-count {
  font-size: 0.8em;
  color: #888;
}

#error-message {
  color: red;
  margin-top: 5px;
}

#add-post-btn {
  background-color: #007bff;
  color: #fff;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1em;
}

#add-post-btn:hover {
  background-color: #0056b3;
}

.remove-btn {
  position: relative;
  float: right;
  margin-left: 5px;
  background-color: red;
  padding: 1px;
  width: 15px;
  text-align: center;
  border-radius: 10px;
  cursor: pointer;
}

.char-count {
  position: relative;
  height: 20px;
  width: 150px;
  text-align: center;
  left: 2px;
  font-size: 13px;
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
  padding: 3px;
  top: 2px;
  margin-bottom: 10px;
  border-radius: 5px;
  display: inline-block;
}

.account-nav ul {
  position: relative;

  list-style-type: none;
  float: left;
  color: rgb(255, 255, 255);
  padding: 12px;
  margin: 5px;
  font-size: 20px;
  cursor: pointer;
  transition: font-size 0.1s ease;
}

.account-nav ul :hover {
  font-size: 26px;
}

.account-nav h4 {
  list-style-type: none;
  position: relative;

  float: left;
  color: rgb(255, 255, 255);
  padding: 12px;
  margin: 5px;
  font-size: 20px;
  cursor: pointer;
  transition: font-size 0.1s ease;
}

.delete-post-btn {
  position: absolute;
  top: 20px;
  right: 20px;
}

.delete-post-btn > button {
  padding: 10px;
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
  border: 1px solid;
  font-size: 12px;
  border-radius: 3px;
  font-weight: 600;
  margin: 2px;
  outline: none;
  cursor: pointer;
}

.error-popup {
  position: absolute;
  background-color: rgb(249, 212, 216);
  height: 25px;
  width: 600px;
  font-size: 14px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  color: rgb(207, 74, 91);
  text-align: center;
  line-height: 25px;
  font-weight: bold;

  top: 0px;
  right: 0px;
  left: 0px;
  margin: auto;
  z-index: 999;
}

.reset-error-btn {
  position: absolute;
  right: 8px;
  top: 3px;
  color: #fff;
  background-color: black;
  width: 20px;
  line-height: 20px;
  font-size: 10px;
  border-radius: 15px;
  cursor: pointer;
}

.input-box,
.input-tags,
.text-box,
.list,
.select-list {
  position: relative;
  width: 87%;
  height: 30px;
  padding: 24px;
  border: 1px solid rgba(0, 0, 0, 0.07);
  border-radius: 5px;
  background-color: #fff;
  margin-bottom: 10px;
  margin-right: 0px;
  display: inline-block;
}

.input-box > .label,
.input-tags > .label,
.text-box > .label,
.list > .label,
.select-list > .label {
  position: absolute;
  font-family: "Open Sans", serif;
  top: 10px;
  left: 15px;
  font-size: 14px;
  font-weight: bold;
}

.input-box > input,
.list > input {
  position: absolute;
  top: 33px;
  left: 10px;
  width: 90%;
  padding: 5px;
  border: none;
  outline: none;
  font-size: 16px;
}

.text-box {
  display: inline;
  height: max-content;
}

.text-box > textarea {
  position: relative;
  top: 10px;
  height: 150px;
  display: block;
  left: 10px;
  width: 95%;
  padding: 5px;
  border: none;
  outline: none;
  font-size: 16px;
  background-color: rgba(0, 0, 0, 0.02);
  margin-bottom: 10px;
  resize: none;
}

.select-list > select {
  position: absolute;
  left: 7px;
  top: 35px;
  width: 500px;
  border: none;
  background-color: transparent;
  outline: none;
  font-size: 12px;
  font-family: "Open Sans", serif;
  border-radius: 0px;
}

.hasError {
  border-color: red;
}

.hasError > .label {
  color: red;
}

.read-post {
  position: relative;
  padding: 30px;
}

.read-post > h1 {
  font-size: 35px;
  font-weight: 600;
}

.read-post > h3 {
  font-size: 18px;
  color: rgba(0, 0, 0, 0.7);
}

.read-post > .separator {
  position: relative;
  height: 1px;
  background-color: red;
}
.read-post > p {
  position: relative;
  top: 25px;
  font-size: 18px;
  white-space: pre-wrap;
}

/* Media Queries for Tablets */
@media only screen and (max-width: 768px) {
  .header {
    flex-direction: column; /* Example change for smaller screens */
    min-width: auto; /* Allow content to be more flexible on smaller screens */
  }

  .app-title a {
    font-size: 16px;
  }

  .app-title-logo img {
    left: 50%;
  }

  .app-title-link a {
    font-size: 24px;
  }

  .app-title p {
    font-size: 14px;
    padding-right: 40px;
    margin-right: 60px;
  }

  .add-post-body {
    width: 70%; /* Adjust width for tablets */
    left: 15%; /* Adjust left position for tablets */
  }
}

/* Media Queries for Phones */
@media only screen and (max-width: 480px) {
  .header {
    flex-direction: column; /* Example change for smaller screens */
    min-width: auto; /* Allow content to be more flexible on smaller screens */
  }

  .app-title a {
    font-size: 4px;
  }

  .app-title-logo img {
    width: 50%;
  }

  .app-title-link a {
    font-size: 4px;
  }

  .app-title p {
    font-size: 12px;
    padding-right: 20px;
    margin-right: 40px;
  }

  .add-post-body {
    width: 100%;
    left: 0%;
  }
  .add-post h2 {
    font-size: 24px;
  }

  .add-post-form {
    padding: 0px;
  }

  #add-post-btn {
    font-size: 16px;
  }
  .add-post-form input {
    width: 100%;
  }
}
